import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import { Row, Col, Form, FormGroup, FormControl, Button, ControlLabel, Alert, Checkbox, Glyphicon  } from 'react-bootstrap';
import { connect } from 'react-redux';
import MultipleDatePicker from "react-multiple-datepicker";
import TimeRange from 'react-time-range';
import TimePicker from 'material-ui/TimePicker';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider'
import moment from 'moment';

import withMeta from '../../../../core/withMeta';
import Widget from '../../../../components/Widget';
import s from './GymProgramNew.scss';
import { createPost } from '../../../../actions/posts';

class GymProgramNew extends React.Component {
  static meta = {
    title: 'Create new Gym Program',
    description: 'About description',
  };

  constructor(props) {
    super(props);

    this.state = {
      title: '',
      content: '',
      slots: [{ name: '' }],
      startTime:'',
      endTime:'',
    };
  }

  handleSlotNameChange = (idx) => (evt) => {
    const newSlots = this.state.slots.map((slot, sidx) => {
      if (idx !== sidx) return slot;
      return { ...slot, name: evt.target.value };
    });
    
    this.setState({ slots: newSlots });
  }

   handleAddSlot = () => {
    this.setState({ slots: this.state.slots.concat([{ name: '' }]) });
  }
  
  handleRemoveSlot = (idx) => () => {
    this.setState({ slots: this.state.slots.filter((s, sidx) => idx !== sidx) });
  }

  changeTitle(event) {
    this.setState({ title: event.target.value });
  }

  changeContent(event) {
    this.setState({ content: event.target.value });
  }

  changeName(event) {
    this.setState({ name: event.target.value });
  }

  changeAreaPinCode(event) {
    this.setState({ areaPinCode: event.target.value });
  }

  changeDescription(event) {
    this.setState({ description: event.target.value });
  }

  changeAvailableSubcriptions(event) {
    this.setState({ availableSubcriptions: event.target.value });
  }

  changeAddress(event) {
    this.setState({ address: event.target.value });
  }

  changeEmail(event) {
    this.setState({ email: event.target.value });
  }

  changeMobile(event) {
    this.setState({ mobile: event.target.value });
  }

  changeLandline(event) {
    this.setState({ landline: event.target.value });
  }

  changeLocation(event) {
    this.setState({ location: event.target.value });
  }

  doCreatePost(e) {
    this.props
      .dispatch(createPost({ title: this.state.title, content: this.state.content }))
      .then(() => this.setState({title: '', content: ''}));
    e.preventDefault();
  }

  render() {
    return (
      <div className={s.root}>
        <ol className="breadcrumb">
          <li><span className="text-muted">YOU ARE HERE</span></li>
          <li className="active">New Gym Program</li>
        </ol>
        <h2>Add Gym Program</h2>
        <Row>
          <Col sm={6}>
            <Widget
              title={
            <span>
          Add Gym Program <span className="fw-semi-bold">Form</span>
            </span>
        }
            >
              <Form horizontal onSubmit={this.doCreatePost.bind(this)}>
                {
                  this.props.message && (
                    <Alert className="alert-sm" bsStyle="info">
                      {this.props.message}
                    </Alert>
                  )
                }
                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Program Name
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Program Name" value={this.state.name}
                                 required
                                 onChange={this.changeName.bind(this)} />
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Program Description
                  </Col>
                  <Col sm={10}>
                    <textarea className="form-control" placeholder="Program Description"
                              value={this.state.address}
                              required
                              onChange={this.changeAddress.bind(this)}>

                    </textarea>
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Discount
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Discount" value={this.state.email}
                                 required
                                 onChange={this.changeEmail.bind(this)} />
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Max Seats
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Max Seats" value={this.state.mobile}
                                 required
                                 onChange={this.changeMobile.bind(this)} />
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Price
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Price per day" value={this.state.landline}
                                 required
                                 onChange={this.changeLandline.bind(this)} />
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Program Dates
                  </Col>
                  <Col sm={10}>
                    <MultipleDatePicker
                  onSubmit={dates => console.log("selected dates ", dates)}
                     />
                  </Col> 
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col smOffset={2} sm={10}>
                   <Checkbox inline>Special Program</Checkbox>
                  </Col>  
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col smOffset={2} sm={10}>
                   <Checkbox inline>Repititive</Checkbox>
                  </Col>  
                </FormGroup>
                
                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Program Prices
                  </Col>
                  <Col sm={10}>
                    <textarea className="form-control" placeholder="Program Prices"
                              value={this.state.description}
                              required
                              onChange={this.changeDescription.bind(this)}>

                    </textarea>
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Closed Days In Week
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Closed Days In Week" value=''
                                 required />
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  
                  {this.state.slots.map((slot, idx) => (  
                  <Row>  
                  <Col componentClass={ControlLabel} sm={2}>
                    Program Slot- {idx+1}
                     </Col> 
                  <Col sm={10} >
                  <table>
                     <tbody>
                        <tr>
                         <td>
                           <MuiThemeProvider>  
                             <TimePicker format="24hr" hintText="Start Time" textFieldStyle={{width: '50%'}}/>
                           </MuiThemeProvider>  
                         </td>
                         <td>
                           <MuiThemeProvider>  
                           <TimePicker format="24hr" hintText="End Time" textFieldStyle={{width: '50%'}}/>
                           </MuiThemeProvider>  
                         </td>
                         <td>
                           <Button bsStyle="xs" onClick={this.handleRemoveSlot(idx)} >
                             <Glyphicon glyph="minus" />
                           </Button>  
                         </td>
                        </tr>
                      </tbody>
                  </table>    
                  </Col>
                  </Row>

                  ))}
                  <Button className="pull-right" bsStyle="primary" onClick={this.handleAddSlot} >
                Add Slot
                  </Button>
                </FormGroup>


                <FormGroup>
                  <Col smOffset={2} sm={10}>
                    <div className="btn-toolbar pull-right">
                      <Button>
                        Cancel
                      </Button>
                      <Button bsStyle="primary" type="submit">
                        {this.props.isFetching ? 'Creating...' : 'Create'}
                      </Button>
                    </div>
                  </Col>
                </FormGroup>
              </Form>
            </Widget>
          </Col>
        </Row>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    isFetching: state.posts.isFetching,
    message: state.posts.message
  };
}

export default connect(mapStateToProps)(withStyles(s)(withMeta(GymProgramNew)));



