import React from 'react';
import { Switch, Route, withRouter } from 'react-router';

import GymDetail from './gymDetails/GymDetail';
import GymProgramNew from './gymPrograms/new/GymProgramNew';

class Gym extends React.Component {

  render() {
    return (
      <Switch>
        <Route path="/app/gym/gymDetail" exact component={GymDetail} />
        <Route path="/app/gym/gymProgram/new" exact component={GymProgramNew} />
      </Switch>
    );
  }
}

export default withRouter(Gym);



