import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import { Row, Col, Form, FormGroup, FormControl, Button, ControlLabel, Alert, Image, Carousel } from 'react-bootstrap';
import { connect } from 'react-redux';
import Popup from "reactjs-popup";
// import  TitlebarGridList from '../../../components/GridList/TitlebarGridList'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import withMeta from '../../../core/withMeta';
import Widget from '../../../components/Widget';
import s from './GymDetail.scss';
import { createPost } from '../../../actions/posts';
import { fetchGYM, createGYM, uploadImages } from '../../../actions/gym';


class GymDetail extends React.Component {
  static meta = {
    title: 'Update Gym Details',
    description: 'About description',
  };

  constructor(props) {
    super(props);
    this.state = {
      name: '',
      description: '',
      areaPinCode: '',
      availableSubcriptions: [],
      closedDaysInWeek: [],
      address: '',
      location: '',
      email: '',
      landline: '',
      mobile: '',
      createdAt:'',
      modifiedAt:'',
      asset: [],
      selectedBookCoverFiles: [],
    };
  }

  componentWillMount() {
    this.props.dispatch(fetchGYM()).then(() => {
           this.setState({ name: this.props.gym.name, description: this.props.gym.description, 
                    areaPinCode: this.props.gym.areaPinCode, 
                    availableSubcriptions: this.props.gym.availableSubcriptions,
                    closedDaysInWeek: this.props.gym.closedDaysInWeek,
                    address: this.props.gym.contactDetail.address, email: this.props.gym.contactDetail.email, 
                    landline: this.props.gym.contactDetail.landline, 
                    mobile: this.props.gym.contactDetail.mobile,
                    location: this.props.gym.location.latitude + ',' + this.props.gym.location.longitude,
                    createdAt: this.props.gym.createdAt,
                    modifiedAt: this.props.gym.modifiedAt,
                    asset: this.props.gym.asset.assestUrls,
                    });
    }
      );
  }


  changeName(event) {
    this.setState({ name: event.target.value });
  }

  changeAreaPinCode(event) {
    this.setState({ areaPinCode: event.target.value });
  }

  changeDescription(event) {
    this.setState({ description: event.target.value });
  }

  changeAvailableSubcriptions(event) {
    this.setState({ availableSubcriptions: event.target.value });
  }

  changeClosedDaysInWeek(event) {
    this.setState({ closedDaysInWeek: event.target.value });
  }

  changeAddress(event) {
    this.setState({ address: event.target.value });
  }

  changeEmail(event) {
    this.setState({ email: event.target.value });
  }

  changeMobile(event) {
    this.setState({ mobile: event.target.value });
  }

  changeLandline(event) {
    this.setState({ landline: event.target.value });
  }

  changeLocation(event) {
    this.setState({ location: event.target.value });
  }

  getNumberOfSelectedFiles() {
    return this.state.selectedBookCoverFiles.filter(el => {
      return el._destroy !== true;
    }).length;
  }


  doUpdateGym(e) {
    var latlong = this.state.location.split(',');
    this.props
      .dispatch(createGYM({ name: this.state.name, description: this.state.description,
                             areaPinCode: this.state.areaPinCode, availableSubcriptions: this.state.availableSubcriptions,
                             closedDaysInWeek: this.state.closedDaysInWeek.split(","),
                             contactDetail: {
                               email: this.state.email,
                               mobile: this.state.mobile,
                               landline: this.state.landline,
                               address: this.state.address,
                             },
                             location: {
                                latitude: latlong[0],
                                longitude: latlong[1]
                             },
                             }));
      // .then(() => this.setState({title: '', content: ''}));
    e.preventDefault();
  }

  uploadImages(){
     console.log("Select Images: ", this.state.selectedBookCoverFiles);
     this.props
      .dispatch(uploadImages(this.state.selectedBookCoverFiles));
  }

  render() {
    
    return (
      <div className={s.root}>
        <ol className="breadcrumb">
          <li><span className="text-muted">YOU ARE HERE</span></li>
          <li className="active">Update Gym Details</li>
        </ol>
        {this.props.isFetching &&
              <tr>
                <td colSpan="100">Loading...</td>
              </tr>
            } 
        <h2>Gym Details</h2> 
        <Row>
          <Col sm={6}>
            <Widget
              title={
            <span>
          Gym Detail <span className="fw-semi-bold">Form</span>
            </span>
        }
            >
              <Form horizontal onSubmit={this.doUpdateGym.bind(this)}>
                {
                  this.props.message && (
                    <Alert className="alert-sm" bsStyle="info">
                      {this.props.message}
                    </Alert>
                  )
                }
                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Name
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Name" value={this.state.name}
                                 required
                                 onChange={this.changeName.bind(this)} />
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Email
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Email" value={this.state.email}
                                 required
                                 onChange={this.changeEmail.bind(this)} />
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Mobile
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Mobile" value={this.state.mobile}
                                 required
                                 onChange={this.changeMobile.bind(this)} />
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Landline
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Landline" value={this.state.landline}
                                 required
                                 onChange={this.changeLandline.bind(this)} />
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Address
                  </Col>
                  <Col sm={10}>
                    <textarea className="form-control" placeholder="Gym Address"
                              value={this.state.address}
                              required
                              onChange={this.changeAddress.bind(this)}>

                    </textarea>
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    GeoLocation
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Latitude,Longitude" value={this.state.location}
                                 required
                                 onChange={this.changeLocation.bind(this)} />
                  </Col>
                 <a href="https://www.latlong.net/" target="_blank" >Use the link to get geo-location </a> 
                </FormGroup>
                

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Description
                  </Col>
                  <Col sm={10}>
                    <textarea className="form-control" placeholder="Description about gym"
                              value={this.state.description}
                              required
                              onChange={this.changeDescription.bind(this)}>

                    </textarea>
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Closed Days In Week
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Closed Days In Week" value={this.state.closedDaysInWeek}
                                 required 
                                 onChange={this.changeClosedDaysInWeek.bind(this)}/>
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Available Subcriptions
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Available Subcriptions" value={this.state.availableSubcriptions}
                                 required
                                 onChange={this.changeAvailableSubcriptions.bind(this)} />
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    PinCode
                  </Col>
                  <Col sm={10}>
                    <FormControl type="text" placeholder="Area PinCode" value={this.state.areaPinCode}
                                 required
                                 onChange={this.changeAreaPinCode.bind(this)} />
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Created At
                  </Col>
                  <Col sm={10}>
                    <FormControl disabled type="text" placeholder="" value={new Date(this.state.createdAt)}
                                 required />
                  </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail">
                  <Col componentClass={ControlLabel} sm={2}>
                    Modified At
                  </Col>
                  <Col sm={10}>
                    <FormControl disabled type="text" placeholder="" value={new Date(this.state.modifiedAt)}
                                 required />
                  </Col>
                </FormGroup>      
                <FormGroup>
                  <Col smOffset={2} sm={10}>
                    <div className="btn-toolbar pull-right">
                      <Button>
                        RESET
                      </Button>
                      <Button bsStyle="primary" type="submit">
                        {this.props.isFetching ? 'Updating...' : 'UPDATE'}
                      </Button>
                    </div>
                  </Col>
                </FormGroup>
              </Form>
            </Widget>
          </Col>
          <Col sm={6}>
          <Row>
          <Carousel>
             {this.state.asset && this.state.asset.map((asset, index) => (
               <Carousel.Item>
               <img width={900} height={500} alt="900x500" src={asset} />
             </Carousel.Item>
            ))}
             <Carousel.Item>
               <img width={900} height={500} alt="900x500" src="https://www.slt-leisure.co.uk/upload/635048014540156250.jpg" />
             </Carousel.Item>
             <Carousel.Item>
               <img width={900} height={500} alt="900x500" src="http://www.gymmaintenance.com.au/wp-content/uploads/2013/02/slider-1.jpg" />
             </Carousel.Item>
          </Carousel> 
           </Row>
           <Row>
              <Widget>
                <FormGroup controlId="formHorizontalEmail">
                   <div className="BookForm">
                        {this.renderUploadCoversButton()}
                        {this.renderSelectedBookCoverFiles()}
                        {this.renderImageSaveButton()}
                   </div>     
                </FormGroup> 
              </Widget>  
           </Row>
          </Col>
        </Row>
      </div>
    );
  }

  renderUploadCoversButton() {
    let numberOfSelectedCovers = this.getNumberOfSelectedFiles();
    return (
      <div>
        <input
          name="covers[]"
          ref={field => (this.bookCoversField = field)}
          type="file"
          disabled={false}
          multiple={true}
          accept="image/*"
          style={{
            width: 0.1,
            height: 0.1,
            opacity: 0,
            overflow: 'hidden',
            position: 'absolute',
            zIndex: -1
          }}
          id="book_covers"
          onChange={e => this.handleBookCoversChange(e)}
          className="form-control"
        />
        <label
          disabled={false}
          className="btn btn-success"
          htmlFor="book_covers">
          <span className="glyphicon glyphicon-cloud-upload" />
          &nbsp; &nbsp;
          {numberOfSelectedCovers === 0
            ? 'Select Images'
            : `${numberOfSelectedCovers} file${numberOfSelectedCovers !== 1
                ? 's'
                : ''} selected`}
        </label>
      </div>
    );
  }

  renderSelectedBookCoverFiles() {
    let fileDOMs = this.state.selectedBookCoverFiles.map((el, index) => {
      if (el._destroy) {
        return null;
      }

      return (
        <li key={index}>
          <div className="photo">
            <img
              width={150}
              src={el.id ? el.url : URL.createObjectURL(el)}
              style={{ alignSelf: 'center' }}
            />
            <div
              className="remove"
              onClick={() => this.removeSelectedBookCoverFile(el, index)}>
              <span style={{ top: 2 }} className="glyphicon glyphicon-remove" />
            </div>
          </div>
          <div className="file-name">
            {el.name}
          </div>
        </li>
      );
    });

    return (
      <ul className="selected-covers">
        {fileDOMs}
      </ul>
    );
  }

  renderImageSaveButton(){
    let numberOfSelectedCovers = this.getNumberOfSelectedFiles();
    if(numberOfSelectedCovers === 0){
          return null;
    }

    return (
      <button
            disabled={false}
            className="btn btn-primary"
            onClick={e => this.uploadImages()}
            >
            Upload
          </button>
    );

  }

  removeSelectedBookCoverFile(cover, index) {
    let { selectedBookCoverFiles } = this.state;
    if (cover.id) {
      selectedBookCoverFiles[index]._destroy = true;
    } else {
      selectedBookCoverFiles.splice(index, 1);
    }

    this.setState({
      selectedBookCoverFiles: selectedBookCoverFiles
    });
  }

  handleBookCoversChange() {
    let selectedFiles = this.bookCoversField.files;
    let { selectedBookCoverFiles } = this.state;
    for (let i = 0; i < selectedFiles.length; i++) {
      selectedBookCoverFiles.push(selectedFiles.item(i));
    } //end for

    this.setState(
      {
        selectedBookCoverFiles: selectedBookCoverFiles
      },
      () => {
        this.bookCoversField.value = null;
      }
    );
  }
}

function mapStateToProps(state) {
  return {
    isFetching: state.gym.isFetching,
    gym: state.gym.gym,
    isUploading: state.gym.isUploading,
    uploadMessage: state.gym.uploadMessage
  };
}

export default connect(mapStateToProps)(withStyles(s)(withMeta(GymDetail)));



